.talks-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  max-width: 1200px;
  overflow: hidden;
}

.talk-item {
  margin: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.5s, opacity 0.5s, box-shadow 0.3s; /* Added box-shadow transition */
  min-width: 100%;
}

.talk-item:hover {
  transform: translateY(-5px); /* Matches the transform on hover for work-experience-card */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Matches the box-shadow on hover for work-experience-card */
}


.talk-item.enter-left {
  transform: translateX(-100%);
  opacity: 0;
}

.talk-item.enter-right {
  transform: translateX(100%);
  opacity: 0;
}

.talk-item.enter-active {
  transform: translateX(0);
  opacity: 1;
}

.talk-item.exit-left {
  transform: translateX(100%);
  opacity: 0;
}

.talk-item.exit-right {
  transform: translateX(-100%);
  opacity: 0;
}

.talk-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
}

.talk-description {
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;
  color: #666;
}

.talk-image {
  width: auto; /* Allow the width to adjust according to the image's aspect ratio */
  height: auto; /* Allow the height to adjust according to the image's aspect ratio */
  max-width: 100%; /* Set maximum width to ensure responsiveness */
  max-height: 400px; /* Set maximum height */
  margin: 0 auto; /* Center the image horizontally */
  margin-bottom: 20px;
  border-radius: 10px; /* Make the corners rounded */
}

.talk-buttons {
  display: flex;
  justify-content: center;
  gap: 10px; /* Add some space between the buttons */
}

.rounded-button {
  display: inline-block;
  padding: 8px 16px;
  border-radius: 20px;
  background-color: #f2f2f2;
  color: #000000;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s;
}

.rounded-button:hover {
  background-color: #dcdcdc;
}

.slideshow-controls {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}

.slideshow-controls button {
  padding: 10px 20px;
  margin: 0 5px;
  border-radius: 5px;
  font-size: 18px;
}

.left-button {
  position: fixed;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.right-button {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
